<!-- 右侧固定导航栏 -->
<template>
  <div class="rightlistBox">
    <section>
      <el-skeleton :rows="3" animated :loading="loading">
        <div class="r1-body">
          <p style="font-size: 24px; margin-bottom: 20px">大只</p>
          <p style="margin-bottom: 20px; color: #4ba596">
            找到对象一定要找一个喜欢你的
          </p>

          <div class="catch-me">
            <div>
              <a href="https://github.com/dazhi10" target="_blank">
                <img
                  class="github_img"
                  src="../assets/img/system/GitHub.png"
                  alt=""
                />
              </a>
            </div>
          </div>
        </div>
      </el-skeleton>
    </section>

    <section class="rs4">
      <h2 class="ui label">热门文章</h2>
      <el-skeleton :rows="browseList.length" animated :loading="loading">
        <ul>
          <li v-for="(item, index) in browseList" :key="'browseList' + index">
            <a :href="'#/DetailArticle?aid=' + item.id" target="_blank">{{
              item.title
            }}</a>
            —— {{ item.viewCount }} 次围观
          </li>
        </ul>
      </el-skeleton>
    </section>

    <!-- 右侧上滑小图片 -->
    <div
      :class="gotoTop ? 'toTop hidden' : 'toTop goTop hidden'"
      @click="toTopfun"
    >
      <img src="../assets/img/system/scroll.png" alt="" />
    </div>
  </div>
</template>

<script>
import { hotArticleList } from "../api/article";
export default {
  data() {
    //选项 / 数据
    return {
      fixDo: false,
      loveme: false,
      gotoTop: false, //返回顶部
      going: false, //是否正在执行上滑动作
      browseList: "", //热门文章 浏览量最多
      artCommentList: "", //最新评论
      loading: true, //是否显示骨架屏
    };
  },
  methods: {
    //事件处理器
    toTopfun: function (e) {
      var that = this;
      this.gotoTop = false;
      this.going = true;
      var timer = setInterval(function () {
        //获取滚动条距离顶部高度
        var osTop =
          document.documentElement.scrollTop || document.body.scrollTop;
        var ispeed = Math.floor(-osTop / 7);
        document.documentElement.scrollTop = document.body.scrollTop =
          osTop + ispeed;

        //到达顶部，清除定时器
        if (osTop == 0) {
          that.going = false;
          clearInterval(timer);
          timer = null;
        }
      }, 10);
    },
    getHotArticleList() {
      hotArticleList().then((response) => {
        this.browseList = response;
        this.timeout = setTimeout(() => {
          this.loading = false;
        }, 700);
      });
    },
  },
  created() {
    //生命周期函数
    var that = this;
    window.onscroll = function () {
      var t = document.documentElement.scrollTop || document.body.scrollTop;
      if (!that.going) {
        if (t > 600) {
          that.gotoTop = true;
        } else {
          that.gotoTop = false;
        }
      }
      if (t > 1200) {
        that.fixDo = true;
      } else {
        that.fixDo = false;
      }
    };
    //查询浏览量最多的10篇文章数据
    this.getHotArticleList();
  },
};
</script>

